import React from "react";
import Image from "next/image";

const HomePage = () => {
  const recommendedItems = [
    { title: "T-shirts with multiple colors", price: "$10.30", image: "/tshirt.png" },
    { title: "Jeans shorts for men", price: "$12.50", image: "/jeanshorts.png" },
    { title: "Brown winter coat", price: "$12.50", image: "/coat.png" },
    { title: "Jeans bag for travel", price: "$34.00", image: "/bag.png" },
    { title: "Leather wallet", price: "$29.00", image: "/wallet.png" },
    { title: "Canon camera black", price: "$99.99", image: "/camera.png" },
    { title: "Headset for gaming", price: "$8.90", image: "/headset.png" },
    { title: "Smart watch silver", price: "$10.30", image: "/smartwatch.png" },
    { title: "Blue wallet for men", price: "$39.00", image: "/bluewallet.png" },
    { title: "Jeans bag for travel", price: "$80.95", image: "/travelbag.png" },
  ];

  const services = [
    { title: "Source from Industry Hubs", image: "/source.png" },
    { title: "Customize Your Products", image: "/customize.png" },
    { title: "Fast, reliable shipping", image: "/shipping.png" },
    { title: "Product monitoring and inspection", image: "/inspection.png" },
  ];

  const suppliersByRegion = [
    { country: "Arabic Emirates", flag: "/uae.png" },
    { country: "Australia", flag: "/australia.png" },
    { country: "United States", flag: "/usa.png" },
    { country: "Russia", flag: "/russia.png" },
    { country: "Italy", flag: "/italy.png" },
    { country: "Denmark", flag: "/denmark.png" },
    { country: "France", flag: "/france.png" },
    { country: "Great Britain", flag: "/uk.png" },
  ];

  return (
    <div className="bg-gray-100 min-h-screen">
      {/* Inquiry Section */}
      <div className="bg-blue-500 p-8 text-white text-center">
        <h1 className="text-2xl sm:text-3xl font-bold mb-4">
          An easy way to send requests to all suppliers
        </h1>
        <p className="text-sm sm:text-base mb-6">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.
        </p>
        <div className="bg-white p-4 rounded-lg max-w-md mx-auto shadow-lg">
          <input
            type="text"
            placeholder="What do you need?"
            className="w-full border border-gray-300 rounded p-2 mb-2"
          />
          <textarea
            placeholder="Type more details"
            className="w-full border border-gray-300 rounded p-2 mb-2"
          ></textarea>
          <div className="flex items-center gap-2 mb-2">
            <input
              type="number"
              placeholder="Quantity"
              className="flex-1 border border-gray-300 rounded p-2"
            />
            <select className="border border-gray-300 rounded p-2">
              <option>Pcs</option>
              <option>Kg</option>
              <option>Liters</option>
            </select>
          </div>
          <button className="w-full bg-blue-500 text-white py-2 rounded hover:bg-blue-600">
            Send Inquiry
          </button>
        </div>
      </div>

      {/* Recommended Items */}
      <div className="container mx-auto p-4">
        <h2 className="text-xl sm:text-2xl font-bold mb-4">Recommended items</h2>
        <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-5 gap-4">
          {recommendedItems.map((item, index) => (
            <div
              key={index}
              className="bg-white border border-gray-300 rounded-lg p-4 flex flex-col items-center text-center shadow-md"
            >
              <Image src={item.image} alt={item.title} width={100} height={100} className="mb-4" />
              <h3 className="font-medium text-sm sm:text-base">{item.title}</h3>
              <p className="text-sm text-gray-500">{item.price}</p>
            </div>
          ))}
        </div>
      </div>

      {/* Extra Services */}
      <div className="container mx-auto p-4">
        <h2 className="text-xl sm:text-2xl font-bold mb-4">Our extra services</h2>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          {services.map((service, index) => (
            <div
              key={index}
              className="bg-white border border-gray-300 rounded-lg p-4 flex flex-col items-center text-center shadow-md"
            >
              <Image src={service.image} alt={service.title} width={50} height={50} className="mb-4" />
              <h3 className="font-medium text-sm sm:text-base">{service.title}</h3>
            </div>
          ))}
        </div>
      </div>

      {/* Suppliers by Region */}
      <div className="container mx-auto p-4">
        <h2 className="text-xl sm:text-2xl font-bold mb-4">Suppliers by region</h2>
        <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 gap-4">
          {suppliersByRegion.map((region, index) => (
            <div
              key={index}
              className="bg-white border border-gray-300 rounded-lg p-4 flex items-center gap-4 shadow-md"
            >
              <Image src={region.flag} alt={region.country} width={30} height={20} />
              <p className="font-medium">{region.country}</p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default HomePage;
